$button-height: 50px;

.btn {
  border-radius: $button-height / 2;
  border: 2px solid $btn-primary-border-color;
  cursor: pointer;
  font-size: 20px;
  font-weight: 700;
  height: $button-height;
  min-width: 170px;
  padding: 0 20px;
  text-transform: uppercase;

  @media (min-width: $tablet) {
    font-size: 24px;
    padding-left: 30px;
    padding-right: 30px;
  }

  &:active {
    box-shadow: inset 0 2px 3px rgba(0,0,0,.125);
  }
  &:focus {
    outline: none;
    box-shadow: inset 0 2px 3px rgba(0,0,0,.125);
  }

  .btn-icon {
    display: inline-block;
    height: 20px;
    font-size: 24px;
    line-height: 30px;
    margin-left: 10px;
    text-transform: lowercase;

    svg {
      fill: $white;
      display: block;
      height: 100%;
      vertical-align: middle;
    }
  }

  .btn-icon.up {
    svg {
      transform: rotate(180deg);
    }
  }
}

.btn-primary {
  color: $btn-primary-color;
  background: $btn-primary-background;
  transition: background-color 200ms ease-out, color 200ms ease-out;

  &:hover {
    color: $btn-primary-color-hover;
    background: $btn-primary-background-hover;

    .btn-icon {
      svg {
        fill: $btn-primary-color-hover;
      }
    }
  }
  &:focus,
  &:active {
    color: $btn-primary-color-hover;
    background: $btn-primary-background-focus;
    border-color: $btn-primary-border-color-focus;
  }
}

.btn-secondary {
  color: $btn-secondary-color;
  border: 2px solid transparent;
  background: $btn-secondary-background;
  transition: background-color 200ms ease-out, color 200ms ease-out;

  &:hover {
    color: $btn-secondary-color-hover;
    background: $btn-secondary-background-hover;
  }
  &:focus,
  &:active {
    color: $btn-secondary-color-hover;
    background: $btn-secondary-background-focus;
    border-color: $btn-secondary-border-color-focus;
  }
}

a.btn {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  line-height: $button-height;
}
