.promo {
  background: #00599C url(../images/hero-bg.png) no-repeat 35% top / cover;
  border-bottom: 6px solid #959595;
  padding-top: 160px;

  @media (min-width: $tablet) {
    background-position: center top;
  }
}

.promo__block {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet) {
    flex-direction: row;
  }
}

.promo__logo {
  // max-width: 100%;
  // overflow: hidden;
  // padding: 10% 20px 30px;
  // position: relative;
  // z-index: 2;
  
  @media (min-width: $tablet) {
    flex: 1;
    padding: 5% 40px 50px 0;
  }
  
  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
    // position: relative;
    max-width: 80%;
    // z-index: 2;
    @media (min-width: $tablet) {
      max-width: 100%;
    }
  }
}

