.promo__text {
  max-width: 100%;
  position: relative;
  text-align: center;
  z-index: 2;
  
  @media (min-width: $tablet) {
    flex: 1;
    text-align: left;
  }
}

.promo__actions {
  margin-top: 40px;
  margin-bottom: 20px;
  text-align: center;

  @media (min-width: $tablet) {
    text-align: left;
    margin-top: 60px;
  }

  .btn {
    // display: block;
    margin: 0 20px 20px;
    vertical-align: top;

    @media (min-width: $tablet) {
      // display: inline-block;
      margin: 0 20px 20px 0;
      width: auto;
    }
  }
}
