@import url('https://fonts.googleapis.com/css?family=Lato:400,700,900');

body,
body * {
  font-family: 'Lato', sans-serif;
}

h1 {
  color: $white;
  font-size: 36px;
  font-weight: 900;
  line-height: 1.2;
  margin-bottom: 10px;

  @media (min-width: $tablet) {
    font-size: 45px;
  }
}

h2 {
  color: $white;
  font-size: 18px;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 20px;

  @media (min-width: $tablet) {
    font-size: 24px;
  }
}

h3 {
  color: $white;
  font-size: 13px;
  font-weight: 700;
  line-height: 19px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 40px;
  max-width: 350px;

  @media (min-width: $tablet) {
    font-size: 16px;
    margin-left: 0;
  }
}

h4 {
  color: $grey;
  font-size: 14px;
  margin-bottom: 20px;
  text-transform: uppercase;

  @media (min-width: $tablet) {
    font-size: 16px;
  }
}

p {
  font-size: 14px;
  line-height: 24px;
  margin-bottom: 20px;

  @media (min-width: $tablet) {
    font-size: 16px;
    line-height: 27px;
  }
}

b {
  font-weight: 600;
}

p + h4 {
  margin-top: 30px;
}

.text-align-center {
  text-align: center;
}

