.footer {
  background: $primary-color;
  padding-top: 30px;
  padding-bottom: 30px;

  @media (min-width: $tablet) {
    padding-top: 70px;
  }
}

.footer__logo {
  flex: 0 0 260px;
  margin-right: 60px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.footer__copyright {
  color: $white;
  text-align: left;

  h3 {
    color: $white;
    font-size: 20px;
    line-height: 28px;
    margin: 0;
  }
  h4 {
    color: $white;
    font-size: 16px;
    line-height: 28px;
    margin: 0;
  }
  h5 {
    color: #a9c8db;
    font-size: 16px;
    line-height: 28px;
  }
}
