/* colors */
$black: #000;
$white: #fff;
$grey-dark: #252525;

$grey:  #3D4351;
$grey-medium: #495061;
$grey-light: #778698;
$grey-lighter: #C8D8E0;
$grey-lightest: #EBECF0;

$primary-color: #0074BB;
$secondary-color: #FF9100;

/* menu */
$menu-background: $white;
$menu-background-mobile: $black;
$menu-items-color: $primary-color;
$menu-items-color-mobile: $white;
$menu-toggle-color: $primary-color;

/* buttons */
$btn-primary-color: $white;
$btn-primary-color-hover: $primary-color;
$btn-primary-background: transparent;
$btn-primary-background-hover: $white;
$btn-primary-background-focus: darken($btn-primary-background-hover, $amount: 10);
$btn-primary-border-color: $white;
$btn-primary-border-color-focus: darken($white, $amount: 10);

$btn-secondary-color: $white;
$btn-secondary-color-hover: $secondary-color;
$btn-secondary-background: $secondary-color;
$btn-secondary-background-hover: $white;
$btn-secondary-background-focus: darken($btn-secondary-background-hover, $amount: 10);
$btn-secondary-border-color: $white;
$btn-secondary-border-color-focus: darken($white, $amount: 10);

/* layout */
$tablet: 768px;
$side-padding: 40px;
$side-padding-mobile: 20px;
$containerWidth: 1024px;

/* forms */
$input-background: $white;
$input-border-color: $white;
$input-color: $grey-dark;
$input-placeholder-color: $primary-color;
$border-radius: 5px;