.contact-us {
  background: url(../images/contact-form-bg.png) no-repeat center / cover;

  h2 {
    font-weight: 900;
  }

  .flex-col {
    display: flex;
    flex-direction: column;
  }
  
  .form-input {
    margin-bottom: 10px;
    width: 100%;
  }
  
  textarea {
    height: 100%;
    min-height: 150px;
    resize: none;
  }

  .btn {
    margin-top: 30px;
  }
}
