.technology {
  background: $grey-dark;

  h2 {
    margin-bottom: 30px;
  }
}

.technology__image {
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

.technology__text {
  color: $white;
  font-size: 18px;
  max-width: 480px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;

  @media (min-width: $tablet) {
    margin-bottom: 10px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  p {
    font-size: 18px;
  }
}

.technology__content {
  background: #575757;
  color: $white;
  padding-top: 30px;
  padding-bottom: 10px;
  position: relative;

  &::before{ 
    box-shadow: 0 10px 10px rgba(0, 0, 0, .3);
    content: '';
    display: block;
    position: absolute;
    top: -20px;
    left: 0;
    right: 0;
    height: 20px;
  }
}

#btn-technology {
  display: block;
  margin-left: auto;
  margin-right: auto;
}